import request from 'superagent';
const secret = process.env.PAYSTACK_SECRET;

const CreateCustomer = ({ name, email, phone }) => new Promise((resolve, res) => {
    request
        .post(`https://api.paystack.co/customer`)
        .set("Authorization", `Bearer ${secret}`)
        .send({
            name,
            email,
            phone
        })
        .end((err, { body }) => {
            if (err) reject(err);
            resolve(body);
        });
});

export const CreateInvoice = (customer, amount, description) => new Promise(async (resolve, reject) => {
    const { status, data: { customer_code } } = await CreateCustomer(customer);

    request
        .post(`https://api.paystack.co/paymentrequest`)
        .set("Authorization", `Bearer ${secret}`)
        .send({
            customer: customer_code,
            description,
            amount,
            due_date: "2018-10-08"
        })
        .end((err, { body }) => {
            if (err) reject(err);
            resolve(body);
        });
});